import React from 'react'
import styles from './ShowreelModal.module.scss'
import showreel from 'src/assets/videos/showreel.mp4'
import { useDispatch } from 'react-redux'
import { closeModal } from 'src/store/modals/actionCreators'
import { useEffect } from 'react'
import { useRef } from 'react'
import { useState } from 'react'

export const ShowreelModal = () => {

  const [currentTime, setCurrentTime] = useState('')
  const [duration, setDuration] = useState('')
  const [progress, setProgress] = useState(0)
  const ref = useRef(null)

  const dispatch = useDispatch()

  useEffect(() => {
    if (ref.current) {
      ref.current.addEventListener('loadedmetadata', onLoadedMeta)
      ref.current.addEventListener('timeupdate', onTimeUpdate)
    }
  }, [ref])

  const onLoadedMeta = function() {
    const minutes = Math.floor(this.duration / 60)
    const remaining = Math.round(this.duration - (60 * minutes))
    
    setDuration(`${minutes >= 10 ? minutes : `0${minutes}`}:${remaining}`)
  }

  const onTimeUpdate = function() {
    const minutes = Math.floor(this.currentTime / 60)

    if (minutes) {
      const remaining = Math.round(this.currentTime - (60 * minutes))

      setCurrentTime(
        `${minutes >= 10 ? minutes : `0${minutes}`}:${remaining > 10 ? remaining : `0${remaining}`}`
      )
    } else {
      const time = Math.round(this.currentTime)
      setCurrentTime(`00:${time >= 10 ? time : `0${time}`}`)
    }

    const percent = (this.currentTime / this.duration) * 100

    setProgress(percent)
  }

  const onClick = function() {
    dispatch(closeModal())
  }

  return (
    <div 
      className={styles.modal} 
      data-cursor-change=''
      onClick={onClick}
    >
      <div 
        className={styles.bottom} 
        // style={{width: `${progress > 15 ? progress : 15}%`}}
      >
        <div className={styles.wrapper} style={{width: `${progress}%`}}>
          <div className={styles.time}>
            {currentTime}/{duration}
          </div>
          <div className={styles.progress}></div>
        </div>
      </div>
      <video ref={ref} className={styles.video} autoPlay loop playsInline>
        <source src={showreel} type='video/mp4' />
      </video>
    </div>
  )
}
