import React from 'react'
import styles from './Menu.module.scss'
import headerStyles from '../Header/Header.module.scss'
import { Button } from '../Button/Button'
import classNames from 'classnames'
import { ReactComponent as Envelope } from 'src/assets/images/envelope.svg'

const items = ['Bahance', 'Презентация', 'Заполнить бриф']

export const Menu = ({links, openModal, isActive, close}) => {
  return (
    <div 
      className={classNames(styles.menu, isActive ? styles.active : null)}
      onClick={close}
    >
      <div className={styles.container} onClick={e => e.stopPropagation()}>
        <ul className={styles.list}>
          {links.map(item =>
            <li className={headerStyles.item} key={item.href}>
              <a className={headerStyles.link} href={item.href}>
                <div className={styles.text}>
                  {item.text}
                </div>
              </a>
            </li>
          )}
        </ul>
        <div className={styles.row}>
          <div className={styles.col}>
            <div className={styles.textUs} href="#">
              <div className={styles.icon}>
                <Envelope />
              </div>
              <a className={styles.link} href="#">
                <div className={styles.text}>Написать нам</div>
              </a>
            </div>
            <div className={styles.wrapper}>
              <a className={styles.link} href="tel:+78124256717">
                <div className={styles.text}>
                  +7 812 425 67 17
                </div>
              </a>
            </div>
          </div>
          <div className={styles.col}>
            <ul className={styles.list}>
              {items.map(item =>
                <li className={headerStyles.item} key={item}>
                  <a href="#" className={styles.link}>{item}</a>
                </li>
              )}
            </ul>
          </div>
        </div>
        <div className={styles.btn}>
          <Button onClick={openModal} >Обсудить проект</Button>
        </div>
      </div>
    </div>
  )
}
