import classNames from 'classnames'
import React from 'react'
import { CustomReveal } from '../CustomReveal/CustomReveal'
import styles from './Row.module.scss'

export const Row = ({label, children, isAlt}) => {
  return (
    <div className={classNames(styles.row, isAlt ? styles.alt : null)}>
      <div className={styles.col}>
        <div className={styles.label}>
          <CustomReveal>
            {label}
          </CustomReveal>
        </div>
      </div>
      <div className={styles.col}>{children}</div>
    </div>
  )
}
