import React from 'react'
import { Text } from '../Text/Text'
import styles from './AwardItem.module.scss'

export const AwardItem = ({award}) => {
  return (
    <div>
      <div className={styles.icon}>
        {award.svg}
      </div>
      <Text>{award.title}</Text>
      <div className={styles.count}>&#215;{award.count}</div>
    </div>
  )
}
