import { useScroll } from 'framer-motion'
import React, { useEffect, useRef, useState } from 'react'
import { Awards } from '../Awards/Awards'
import { Clients } from '../Clients/Clients'
import { Expertise } from '../Expertise/Expertise'
import { Intro } from '../Intro/Intro'
import { Projects } from '../Projects/Projects'
import { VideoBg } from '../VideoBg/VideoBg'

export const Main = ({isMobile, scrollContainer}) => {

  const projectsRef = useRef(null)
  const expertiseRef = useRef(null)
  const [fadeResult, setFadeResult] = useState(0)
  const [blurResult, setBlurResult] = useState(0)

  const fadeOut = useScroll({
    target: projectsRef,
    offset: ["start center", "start start"],
    container: scrollContainer,
  })

  const blur = useScroll({
    target: expertiseRef,
    offset: ["start end", "start start"],
    container: scrollContainer,
  })

  useEffect(() => {
    fadeOut.scrollYProgress.onChange(res => {
      setFadeResult(res)
    })
    blur.scrollYProgress.onChange(res => {
      setBlurResult(res)
    })
  }, [])

  return (
    <div className='main'>
      <VideoBg fadeResult={fadeResult} blurResult={blurResult} />
      <Intro isMobile={isMobile} scrollContainer={scrollContainer} />
      <Expertise node={expertiseRef} />
      <Clients />
      <Awards />
      <Projects node={projectsRef} />
    </div>
  )
}
