import React, { useEffect, useState } from 'react'
import styles from './Showreel.module.scss'
import { ReactComponent as Play } from 'src/assets/images/play.svg'
import { useScroll } from 'framer-motion'
import { Picture } from '../Picture/Picture'
import { useDispatch } from 'react-redux'
import { openModal } from 'src/store/modals/actionCreators'
import { useRef } from 'react'

const links = [
  {text: 'Bahance', href: '#'},
  {text: 'Презентация', href: '#2'},
]
export const Showreel = ({node, isMobile, progress, scrollContainer}) => {
  // const [result, setResult] = useState(1)
  const [scaleProgress, setScaleProgress] = useState(0)
  const targetRef = useRef(null)

  const { scrollYProgress } = useScroll({
    target: targetRef,
    offset: ['start end', 'end start'],
    container: scrollContainer,
  })

  // const { scrollYProgress } = useScroll({
  //   target: node,
  //   offset: [`start center`, `start ${node.current.offsetTop}px`],
  //   container: scrollContainer
  // })

  const dispatch = useDispatch()

  useEffect(() => {
    scrollYProgress.onChange(res => {
      setScaleProgress(res)
    })
  }, [])

  // useEffect(() => {
  //   if (isMobile) {
  //     setResult(0)
  //   } else {
  //     scrollYProgress.onChange(res => {
  //       setResult(res)
  //     })
  //   }
  // }, [])

  return (
    <div ref={targetRef}>
      <div className={styles.row}>
        <div className={styles.col}>
          <ul className={styles.list} style={{transform: `translateY(-${394 * progress}%)`}}>
            {links.map(item => 
              <li className={styles.item} key={item.href}>
                <a className={styles.link} href={item.href}>{item.text}</a>
              </li>
            )}
          </ul>
        </div>
        <div className={styles.col} style={{paddingLeft: `${25.3 - (25.3 * progress)}%`}} id="showreel">
          <div 
            className={styles.container} 
            data-cursor-change=''
            onClick={() => dispatch(openModal())}
          >
            <div className={styles.overlay}></div>
            <div className={styles.layout} data-scroll='' data-scroll-speed='-2' data-scroll-target="#showreel">
              <div className={styles.image} style={{transform: `scale(${1.2 - (0.2 * scaleProgress)})`}}>
                <Picture fileName={'showreel.jpg'} />
              </div>
            </div>
            <div className={styles.wrapper}>
              <div className={styles.label}>
                <div className={styles.icon}>
                  <Play />
                </div>
                <div className={styles.text}>Showreel</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
