import React from 'react'
import styles from './CategoryItem.module.scss'
import classNames from "classnames";

export const CategoryItem = ({item, onClick, currentActive}) => {
  if (currentActive) {
    return (
      <div 
        onClick={() => onClick(item)}
        className={classNames(styles.item, currentActive === item ? [styles.active] : null)}
      >
        {item}
      </div>
    )
  } else {
    return (
      <div className={classNames(styles.item, styles.small)}>{item}</div>
    )
  }
}
