import React from 'react'
import styles from './Intro.module.scss'
import { animated, useSprings } from 'react-spring'
import { useEffect } from 'react'
import { useSelector } from 'react-redux'

export const IntroTitle = ({children}) => {

  const arr = children.split('')

  const { isLoaded } = useSelector(state => state.content)

  const [springs, api] = useSprings(arr.length, 
    (i) => ({
      y: '100%',
      rotateX: -90,
      opacity: 0,
    })
  )

  useEffect(() => {
    if (isLoaded) {
      api.start(i => ({
        y: '0%',
        rotateX: 0,
        opacity: 1,
        delay: i * 3,
        config: {
          duration: 300
        }
      }))
    }
  }, [isLoaded])

  return (
    <div className={styles.flex}>
      {springs.map((style, i) => (
        <animated.div className={styles.test} key={i} style={style}>
          <animated.div className={styles.title}>{arr[i]}</animated.div>
        </animated.div>
      ))}
    </div>
  )
}
