import React, { useRef, useEffect, useState } from 'react'
import { Container } from '../Container/Container'
import { IntroLine } from '../IntroLine/IntroLine'
import { Row } from '../Row/Row'
import { Showreel } from '../Showreel/Showreel'
import { CustomReveal } from '../CustomReveal/CustomReveal'
import styles from './Intro.module.scss'
import showreelStyles from '../Showreel/Showreel.module.scss'
import { IntroTitle } from './IntroTitle'
import { useScroll } from 'framer-motion'

const data = ['Retail', 'FMCG', 'E-commerce', 'IT & Telecom', 'Промышленность', ]
const links = [
  { href: 'showreel', text: 'Showreel' },
  { href: 'Услуги', text: 'Услуги' },
  { href: 'clients', text: 'Клиенты и награды' },
  { href: 'cases', text: 'Кейсы' },
  { href: 'contacts', text: 'Контакты' },
]
const showreelLinks = [
  {text: 'Bahance', href: '#'},
  {text: 'Презентация', href: '#2'},
]

export const Intro = ({isMobile, scrollContainer}) => {

  const [progress, setProgress] = useState(0)
  
  const showreelRef = useRef(null)
  const titleRef = useRef(null)
  
  const { scrollYProgress } = useScroll({
    target: titleRef,
    offset: [`start ${window.innerHeight * 0.447}px`, "start start"],
    container: scrollContainer
  })

  useEffect(() => {
    scrollYProgress.onChange(latest => {
      setProgress(latest)
    })
  }, [])

  return (
    <div className={styles.intro} data-scroll-section=''>
      <Container>
        {isMobile
          ?
            <>
              <ul className={styles.links}>
                {links.map(item => 
                  <li key={item.href}>
                    <CustomReveal>
                      <a href={item.href} className={styles.link}>{item.text}</a>
                    </CustomReveal>
                  </li>
                )}
              </ul>
              <div className={styles.content}>
                <IntroTitle>Digital</IntroTitle>
                <div className={styles.col} ref={titleRef}>
                  {titleRef.current && 
                    <IntroLine node={titleRef} />}
                </div>
              </div>
              <div className={styles.row}>
                <div className={styles.col}>
                  <ul className={styles.list}>
                    {data.map(item =>
                      <li className={styles.item} key={item}>{item}</li>
                    )}
                  </ul>
                </div>
                <div className={styles.col}>
                  <ul className={showreelStyles.list}>
                    {showreelLinks.map(item => 
                      <li className={showreelStyles.item} key={item.href}>
                        <a className={showreelStyles.link} href={item.href}>{item.text}</a>
                      </li>
                    )}
                  </ul>
                </div>
              </div>
            </>
          :
            <>
              <ul className={styles.list}>
                {data.map(item =>
                  <li className={styles.item} key={item}>
                    <CustomReveal>
                      {item}
                    </CustomReveal>
                  </li>
                )}
              </ul>
              <div className={styles.bot}>
                <div className={styles.wrapper} ref={titleRef}>
                  <Row>
                    <IntroTitle>Digital</IntroTitle>
                    <div className={styles.col}>
                      {titleRef.current && 
                        <IntroLine progress={progress} />}
                    </div>
                  </Row>
                </div>
              </div>
            </>
        }
        <div ref={showreelRef} className={styles.showreel}>
          {showreelRef.current &&
            <Showreel 
              isMobile={isMobile} 
              progress={progress} 
              node={showreelRef} 
              scrollContainer={scrollContainer} 
            />}
        </div>
      </Container>
    </div>
  )
}
