import React from 'react'
import { useInView } from 'react-intersection-observer'
import { useSelector } from 'react-redux'
import { useSpring, animated } from 'react-spring'
import styles from './CustromReveal.module.scss'

export const CustomReveal = ({children}) => {

  const { isLoaded } = useSelector(state => state.content)
  const [ref, inView] = useInView({
    triggerOnce: true,
  })
  const spring = useSpring({
    to: {
      skewY: inView && isLoaded ? 0 : 10,
      scaleX: inView && isLoaded ? 1 : 0.9,
      y: inView && isLoaded ? 0 : 100,
    },
    config: {
      duration: 500,
    }
  })


  return (
    <div className={styles.wrapper} ref={ref}>
      <animated.div style={spring}>{children}</animated.div>
    </div>
  )
}
