const { CONTENT_IS_LOADED } = require("./actions")


const initialState = {
  isLoaded: false,
}

const contentReducer = (state = initialState, action) => {
  switch (action.type) {
    case CONTENT_IS_LOADED:
      return {isLoaded: true};
    default:
      return state;
  }
}

export default contentReducer;