import locomotiveScroll from "locomotive-scroll";

document.body.onload = () => {
  const wrapper = document.querySelector('.wrapper')
  const locScroll = new locomotiveScroll({
    el: wrapper,
    smooth: true,
    smartphone: {
      smooth: false,
    },
    tablet: {
      smooth: false,
    },
    getDirection: true,
    reloadOnContextChange: false,
    resetNativeScroll: false,
    scrollbarContainer: wrapper
  });

  const bodyHeight = wrapper.offsetHeight
  const scrollContainer = wrapper.querySelector('[data-scroll-container]')
  const scrollBody = wrapper.querySelector('[data-scroll-body]')

  scrollBody.style.height = `${bodyHeight}px`

  locScroll.on('scroll', event => {
    const posY = event.scroll.y

    scrollContainer.scrollTop = posY
  })
}