import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import { useSprings, animated } from 'react-spring'
import styles from './Count.module.scss'

export const CountNumber = ({number, index, duration}) => {
  const count = index > 1 ? 10 : number
  const realDuration = duration / (count  * index + 1) 
  const [numbers, setNumbers] = useState([])

  useEffect(() => {
    const arr = []
    for (let i = 0; i < count * index; i++) {
      const num = i >= 10 ? i % 10 : i
      arr.push(num)
    }
    arr.push(number)
    setNumbers(arr)
  }, [])

  const to = (i) => ({
    y: (i+1) === numbers.length ? '0%' : '-100%',
    delay: i * (realDuration / 2),
  })
  const from = () => ({ y: '100%' })
  
  const [props, api] = useSprings(numbers.length, i => ({
    ...to(i),
    from: from(i),
    config: {
      duration: numbers.length === (i+1) ? realDuration / 2 : realDuration
    }
  }))

  return (
    <div>
      {props.map(({y}, i) => 
        <animated.div key={i} style={{y}} className={styles.number}>
          {numbers[i]}
        </animated.div>
      )}
    </div>
  )
}
