import React from 'react'
import { Text } from '../Text/Text'
import { CategoryList } from '../CategoryList/CategoryList'
import styles from './Project.module.scss'

export const Project = ({ project }) => {
  return (
    <div className={styles.item} data-sketch='' data-cursor-change=''>
      <div className={styles.container}>
        <div className={styles.content} data-sketch-content=''>
          <div 
            className={styles.slider} 
            data-images={`["${require('src/assets/images/projects/chl.jpg')}"]`}
            data-displacement=''
            data-sketch-slider=''
          >
          </div>
        </div>
        <video className={styles.video} src={require(`src/assets/videos/novikov.mp4`)} loop muted data-played='false' data-sketch-video=''></video>
      </div>
      <div className={styles.bot}>
        <div className={styles.col}>
          <div className={styles.title}>{project.title}</div>
        </div>
        <div className={styles.col}>
          <Text>{project.description}</Text>
        </div>
      </div>
      {project.awards 
        ?
          <div className={styles.list}>
            <CategoryList items={project.awards} />
          </div>
        :
          null
      }
    </div>
  )
}
