import React from 'react'
import styles from './File.module.scss'
import { ReactComponent as Clip } from 'src/assets/images/clip.svg'
import { ReactComponent as Trash } from 'src/assets/images/trash.svg'
import classNames from 'classnames'

export const File = ({file, onChange, onClick}) => {

  return (
    <div className={styles.file}>
      {file 
        ?
          <div className={styles.item}>
            <div className={classNames(styles.text, styles.left)}>{file.name}</div>
            <div 
              className={classNames(styles.icon, styles.btn)} 
              onClick={onClick}
            >
              <Trash />
            </div>
          </div>
        :
          <label className={styles.label}>
            <input 
              className={styles.input} 
              type="file" 
              onChange={onChange} 
            />
            <div className={classNames(styles.icon, styles.left)}>
              <Clip />
            </div>
            <div className={styles.text}>Прикрепить файл</div>
          </label>
      }
    </div>
  )
}
