import React, { useState } from 'react'
import { Button } from '../Button/Button'
import { File } from '../File/File'
import { FormItem } from './FormItem'
import styles from './Form.module.scss'

const items = [
  {placeholder: 'Вас зовут', type: 'text'},
  {placeholder: 'Телефон для связи', type: 'phone'},
  {placeholder: 'Почта', type: 'email'},
  {placeholder: 'Немного о проекте', type: 'text'},
]

export const Form = ({onSubmit}) => {

  const [file, setFile] = useState(null)

  return (
    <form onSubmit={onSubmit} className={styles.form} action="/">
      {items.map(item =>
        <FormItem key={item.placeholder} item={item} />
      )}
      <div className={styles.bot}>
        <File 
          file={file}
          onChange={e => setFile(e.target.files[0])}
          onClick={() => setFile(null)}
        />
        <div className={styles.button}>
          <Button>Отправить</Button>
        </div>
      </div>
    </form>
  )
}
