import React from 'react'
import { Container } from '../Container/Container'
import { Row } from '../Row/Row'
import { Section } from '../Section/Section'
import { Title } from '../Title/Title'
import { Text } from '../Text/Text'
import styles from './Expertise.module.scss'
import { CustomReveal } from '../CustomReveal/CustomReveal'
import { Count } from '../Count/Count'
import { useInView } from 'react-intersection-observer'

export const Expertise = ({node}) => {

  const data = ['Предпроектные исследования', 'UX/UI-дизайн ', 'Разработка сайтов и приложений', 'No-сode-разработка', 'Аутстафф дизайнеров и разработчиков', ]
  
  const [firstRef, firstInView] = useInView()
  const [secondRef, secondInView] = useInView()

  return (
    <Section id='expertise'>
      <Container>
        <Row label="Экспертиза">
          <div className={styles.row} ref={node}>
            <div className={styles.col}>
              <ul>
                {data.map(item =>
                  <li className={styles.item} key={item}>
                    <CustomReveal>
                      {item}
                    </CustomReveal>
                  </li>
                )}
              </ul>
            </div>
            <div className={styles.col}>
              <div className={styles.stat}>
                <div className={styles.number}>
                  <div className={styles.wrapper}>
                    <div className={styles.layout}>
                      <Title>350</Title>
                    </div>
                    <div className={styles.title}>
                      <Title>
                        {firstInView &&
                          <Count number={350} />}
                      </Title>
                    </div>
                  </div>
                  <Title>
                    +
                  </Title>
                </div>
                <CustomReveal>
                  <Text node={firstRef}>проектов</Text>
                </CustomReveal>
              </div>
              <div className={styles.stat}>
                <div className={styles.wrapper}>
                  <div className={styles.layout}>
                    <Title>
                      13
                    </Title>
                  </div>
                  <div className={styles.title}>
                    <Title>
                      {secondInView &&
                        <Count number={13} />}
                    </Title>
                  </div>
                </div>
                <CustomReveal>
                  <Text node={secondRef} extraClass={styles.text}>лет на рынке</Text>
                </CustomReveal>
              </div>
            </div>
          </div>
        </Row>
      </Container>
    </Section>
  )
}
