import React, { useState } from 'react'
import { Container } from '../Container/Container'
import styles from './Header.module.scss'
import { ReactComponent as Logo } from 'src/assets/images/logo.svg'
import { ReactComponent as Envelope } from 'src/assets/images/envelope.svg'
import { Button } from '../Button/Button'
import { Menu } from '../Menu/Menu'

const links = [
  { href: 'showreel', text: 'Showreel' },
  { href: 'Услуги', text: 'Услуги' },
  { href: 'clients', text: 'Клиенты и награды' },
  { href: 'cases', text: 'Кейсы' },
  { href: 'contacts', text: 'Контакты' },
]

export const Header = ({openModal}) => {

  const [isMenuOpen, setIsMenuOpen] = useState(false)

  return (
    <div className={styles.header}>
      <div className={styles.inner}>
        <Container>
          <div className={styles.desktop}>
            <div className={styles.row}>
              <div className={styles.col}>
                <div className={styles.logo}>
                  <Logo className='qwe' />
                </div>
              </div>
              <div className={styles.col}>
                <ul className={styles.list}>
                  {links.map(item =>
                    <li className={styles.item} key={item.href}>
                      <a className={styles.link} href={item.href}>
                        <div className={styles.text}>
                          {item.text}
                        </div>
                      </a>
                    </li>
                  )}
                </ul>
              </div>
              <div className={styles.col}>
                <div className={styles.left}>
                  <div className={styles.textUs} href="#">
                    <div className={styles.icon}>
                      <Envelope />
                    </div>
                    <a className={styles.link} href="#">
                      <div className={styles.text}>Написать нам</div>
                    </a>
                  </div>
                  <div className={styles.wrapper}>
                    <a className={styles.link} href="tel:+78124256717">
                      <div className={styles.text}>
                        +7 812 425 67 17
                      </div>
                    </a>
                  </div>
                </div>
                <div className={styles.right}>
                  <div className={styles.button}>
                    <Button onClick={openModal}>Обсудить проект</Button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className={styles.mobile}>
            <div className={styles.row}>
              <div className={styles.col}>
                <div className={styles.logo}>
                  <Logo className='qwe' />
                </div>
              </div>
              <div className={styles.col}>
                <div className={styles.menu}>
                  <div 
                    className={styles.burger} 
                    onClick={() => setIsMenuOpen(!isMenuOpen)}
                  >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Container>
      </div>
      <Menu 
        openModal={openModal} 
        close={() => setIsMenuOpen(false)} 
        links={links} 
        isActive={isMenuOpen} 
      />
    </div>
  )
}
