

const initCursor = () => {
  const cursor = document.querySelector('.cursor-wrapper')
  const outer = cursor.querySelector('.outer')
  const text = cursor.querySelector('.text-wrapper')
  let isSmall = true

  outer.style.setProperty('--scale-ratio', 1)
  let interval;

  function scaleCursor(speed) {
    let size = +outer.style.getPropertyValue('--scale-ratio')
    let end = 6.5;
    interval = setInterval(() => {
      if (size === end) {
        clearInterval(interval);
        return;
      }
      size += 0.25;
      outer.style.setProperty('--scale-ratio', size)
    }, speed);
  }
  function decreaseCursor(speed) {
    let size = +outer.style.getPropertyValue('--scale-ratio')
    let end = 1;
    interval = setInterval(() => {
      if (size === end) {
        clearInterval(interval);
        return;
      } 
      size -= 0.25;
      outer.style.setProperty('--scale-ratio', size)
    }, speed);
  }
  
  window.addEventListener('mousemove', event => {
    const posX = event.clientX
    const posY = event.clientY
    outer.style.transform = `translate(0%, -50%) translate(${posX}px, ${posY}px) scale(var(--scale-ratio))`
    text.style.transform = `translate(0%, -50%) translate(${posX}px, ${posY}px)`
  })

  window.addEventListener('mouseover', event => {
    const target = event.target.closest('[data-cursor-change]')
    
    if (target && isSmall) {
      clearInterval(interval)
      isSmall = false
      cursor.classList.add('expanded')
      document.body.classList.add('js-custom-cursor')
      scaleCursor(7)
    }
  })
  window.addEventListener('mouseout', event => {
    const target = event.target.closest('[data-cursor-change]')
    
    if (target && !isSmall) {
      clearInterval(interval)
      isSmall = true
      cursor.classList.remove('expanded')
      document.body.classList.remove('js-custom-cursor')
      decreaseCursor(7)
    }
  })
}

export { initCursor };