import classNames from 'classnames'
import React, { useState } from 'react'
import { Container } from '../Container/Container'
import { Picture } from '../Picture/Picture'
import { Row } from '../Row/Row'
import { Section } from '../Section/Section'
import styles from './Clients.module.scss'
import rowStyles from '../Row/Row.module.scss'
import { ReactComponent as Magnit } from 'src/assets/images/clients/magnit.svg'
import { ReactComponent as Novikov } from 'src/assets/images/clients/novikov.svg'
import { ReactComponent as Mts } from 'src/assets/images/clients/mts.svg'
import { ReactComponent as Lukoil } from 'src/assets/images/clients/lukoil.svg'
import { ReactComponent as Geekbrains } from 'src/assets/images/clients/geekbrains.svg'
import { ReactComponent as Theatre } from 'src/assets/images/clients/theatre.svg'
import { ReactComponent as Chl } from 'src/assets/images/clients/chl.svg'
import { ReactComponent as MilkFactory } from 'src/assets/images/clients/milk-factory.svg'
import { ReactComponent as Giaginsky } from 'src/assets/images/clients/giaginsky.svg'
import { ReactComponent as SetlGroup } from 'src/assets/images/clients/setl-group.svg'
import { ReactComponent as Sanfor } from 'src/assets/images/clients/sanfor.svg'
import { ReactComponent as MosEnergy } from 'src/assets/images/clients/mos-energy.svg'
import { ReactComponent as Scandinavia } from 'src/assets/images/clients/scandinavia.svg'
import { ReactComponent as MosEnergyProject } from 'src/assets/images/clients/mos-energy-project.svg'
import { ReactComponent as Rublevsky } from 'src/assets/images/clients/rublevsky.svg'
import { ReactComponent as FirstFurniture } from 'src/assets/images/clients/first-furniture.svg'
import { ReactComponent as Alfahorizont } from 'src/assets/images/clients/alfahorizont.svg'
import { ReactComponent as Salini } from 'src/assets/images/clients/salini.svg'
import { ReactComponent as Dubki } from 'src/assets/images/clients/dubki.svg'
import { useSpring, animated, easings } from 'react-spring'

export const Clients = () => {

  const firstLine = [
    {
      svg : [<Magnit />, <SetlGroup />]
    },
    {
      svg : [<Novikov />, <Sanfor />]
    },
    {
      svg : [<Mts />, <MosEnergy />]
    },
    {
      svg : [<Lukoil />, <Scandinavia />]
    },
    {
      svg : [<Geekbrains />, <MosEnergyProject />]
    },
  ]

  const secondLine = [
    {
      svg : [<Theatre />, <Rublevsky />]
    },
    {
      svg : [<Chl />, <FirstFurniture />]
    },
    {
      isImage: true,
      path: 'vysotskaya.jpg',
      svg : <Alfahorizont />
    },
    {
      svg : [<MilkFactory />, <Salini />]
    },
    {
      svg : [<Giaginsky />, <Dubki />]
    },
  ]

  const first = useSpring({
    from: {
      y: "0%",
    },
    to: {
      y: "-100%"
    },
    delay: 2000,
    config: {
      easing: easings.easeInOutCubic,
      duration: 1000
    },
    loop: { reverse: true },
    reset: true,
  })

  const second = useSpring({
    from: {
      y: "100%",
    },
    to: {
      y: "0%"
    },
    delay: 2000,
    config: {
      easing: easings.easeInOutCubic,
      duration: 1000
    },
    loop: { reverse: true },
    reset: true,
  })
  
  return (
    <Section id='clients'>
      <Container>
        <div className={styles.row}>
          <div className={styles.col}>
            <div className={rowStyles.label}>Клиенты</div>
          </div>
          <div className={styles.col}>
            <div className={styles.line}>
              <div className={styles.grid}>
                {firstLine.map((item, i) => 
                  <div className={styles.item} key={i}>
                    <animated.div className={styles.svg} style={first} >{item.svg[0]}</animated.div>
                    <animated.div className={styles.svg} style={second} >{item.svg[1]}</animated.div>
                  </div>
                )}
              </div>
            </div>
            <div className={styles.line}>
              <div className={styles.grid}>
                {secondLine.map((item, i) => 
                  <div className={classNames(styles.item, styles.big)} key={i}>
                    {item.isImage
                      ?
                        <>
                          <animated.div className={styles.image} style={first}>
                            <Picture fileName={item.path}  folder='clients/' />
                          </animated.div>
                          <animated.div className={styles.svg} style={second}>{item.svg}</animated.div>
                        </>
                      :
                        <>
                          <animated.div className={styles.svg} style={first}>{item.svg[0]}</animated.div>
                          <animated.div className={styles.svg} style={second}>{item.svg[1]}</animated.div>
                        </>
                    }
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </Container>
    </Section>
  )
}
