import React from 'react'
import { AwardItem } from '../AwardItem/AwardItem'
import { Container } from '../Container/Container'
import { Row } from '../Row/Row'
import { Section } from '../Section/Section'
import { Title } from '../Title/Title'
import { Text } from '../Text/Text'
import styles from './Awards.module.scss'
import { ReactComponent as RuNet } from 'src/assets/images/award-1.svg'
import { ReactComponent as GoldenSite } from 'src/assets/images/award-2.svg'
import { ReactComponent as Tagline } from 'src/assets/images/award-3.svg'
import { ReactComponent as Awwwards } from 'src/assets/images/award-4.svg'
import { ReactComponent as CssDesign } from 'src/assets/images/award-5.svg'
import { ReactComponent as Behance } from 'src/assets/images/award-6.svg'

export const Awards = () => {

  const data = [
    { svg: <RuNet />, title: 'Рейтинг Рунета', count: 6 },
    { svg: <GoldenSite />, title: 'Золотой сайт', count: 7 },
    { svg: <Tagline />, title: 'Tagline Awards', count: 14 },
    { svg: <Awwwards />, title: 'AWWWARDS', count: 3 },
    { svg: <CssDesign />, title: 'CSS Design Awards', count: 6 },
    { svg: <Behance />, title: 'Behance', count: 6 },
  ]

  return (
    <Section id='awards'>
      <Container>
        <Row label='Награды' isAlt={true}>
          <div className={styles.top}>
            <div className={styles.mobile}>
              <div className={styles.label}>Награды</div>
            </div>
            <div className={styles.content}>
              <Title>40+</Title>
              <Text>Наград на отечественных <br /> и международных конкурсах</Text>
            </div>
          </div>
          <div className={styles.wrapper}>
            <div className={styles.grid}>
              {data.map(item =>
                <AwardItem key={item.title} award={item} />
              )}
            </div>
          </div>
        </Row>
      </Container>
    </Section>
  )
}
